import Bmob from "hydrogen-js-sdk";
import staffs from "@/utils/staffs.js"

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		uniSegmentedControl,
		faIcon,
		loading,
		uniNavBar,
		uniIcon
	},
	data() {
		return {
			is_choose: false,
			loading: true,
			stocks: null,
			items: ['已启用', '未启用'],
			current: 0,
			disabled: false,
			type: '', //'out_choose'是调拨时的选择
		}
	},

	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');

		console.log(options)
		if (options.type == "choose" || options.type == "out_choose" || options.type == "choose_more") {
			that.is_choose = true
			that.type = options.type
		}
	},
	onShow() {
		that.getstock_list()
	},
	onUnload() {
		search_text = ""
	},
	methods: {
		//预览图片
		priviewImg(url){
			uni.previewImage({
				current:url,
				urls: [url],
			});
		},

		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index

				if (index == 0) {
					that.disabled = false,
						that.getstock_list()
				} else if (index == 1) {
					that.disabled = true,
						that.getstock_list()
				}
			}
		},

		//点击仓库去到详情
		goto_detail(stock) {
			uni.setStorageSync("stock", stock)
			uni.navigateTo({
				url: "detail/detail"
			})
		},

		//选择此仓库
		select_this(item) {
			let warehouse;
			if (that.type == "choose_more") {
				warehouse = uni.getStorageSync("warehouse") || [];
			} else {
				warehouse = []
			}

			let _stocks = {};

			_stocks.stock = item;
			_stocks.reserve = 0;
			_stocks.warning_num = 0;

			if (JSON.stringify(warehouse).indexOf(JSON.stringify(_stocks)) == -1) {
				warehouse.push(_stocks);
				if (that.type == "out_choose") {
					uni.setStorageSync("out_warehouse", warehouse)
					uni.navigateBack({
						delta: 1
					})
				} else {
					uni.setStorageSync("warehouse", warehouse)
					uni.navigateBack({
						delta: 1
					})
				}
			} else {
				uni.showToast({
					title: "已选择此仓库",
					icon: "none"
				})
			}

		},

		//编辑操作
		edit(item) {
			uni.setStorageSync("warehouse", item);
			uni.setStorageSync("charge", item.charge);
			uni.setStorageSync("shop", item.shop);
			uni.navigateTo({
				url: "add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '请谨慎删除，一旦删除，数据不能恢复，是否删除此仓库',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("stocks");
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				that.getstock_list()
			}).catch(err => {
				console.log(err)
			})
		},

		//前去添加仓库
		goto_add() {
			let user = uni.getStorageSync("user")
			let identity = uni.getStorageSync("identity")
			if(user.is_vip || that.stocks.length <2){
				uni.navigateTo({
					url: "add/add"
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '非会员最多上传2个仓库',
						confirmText:"充值会员",
				    success: function (res) {
				        if (res.confirm) {
				            if(identity == 1){
				            	uni.navigateTo({
				            		url:"/pages/mine/vip/vip"
				            	})
				            }else{
				            	uni.showToast({
				            		title:"员工不能充值",
				            		icon:"none"
				            	})
				            }
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				})
			}
		},

		//输入内容筛选
		input_confirm(e) {
			search_text = e.detail.value
			that.getstock_list();
		},

		//得到仓库列表
		getstock_list: function() {
			const query = Bmob.Query("stocks");
			query.order("-num");
			query.include("charge", "shop")
			query.equalTo("parent", "==", uid);
			query.equalTo("disabled", "!=", !that.disabled);
			if (search_text) {
				query.equalTo("stock_name", "==", {
					"$regex": "" + search_text + ".*"
				});

			}
			query.find().then(res => {
				//console.log(res)
				that.loading = false;
				let stocks = res;
				
				let _warehouse = []
				for (let item of stocks) {
					let warehouse = {}
					warehouse.name = item.stock_name
					warehouse.objectId = item.objectId
					_warehouse.push(warehouse)
				}
				uni.setStorageSync("_warehouse", _warehouse)

				uni.getStorage({
					key: 'identity',
					success: function(res) {
						if (res.data == "2") {
							staffs.get_satffAuth().then(res => {
								console.log(res)
								let manange_stocks = []
								for (let item of res.stocks) {
									for (let stock of stocks) {
										if (stock.objectId == item) {
											manange_stocks.push(stock)
										}
									}
								}
								that.stocks = manange_stocks
							});
						} else if (res.data == "1") {
							that.stocks = stocks
						}
					},
				})
			});
		},

	}
}
